/* ==================
Topbar CSS
===================== */
.app-topbar {
  position: sticky;
  top: 0;
  z-index: 1000;
  background: var(--bs-topbar-bg);
  min-height: var(--bs-topbar-height);
  margin-left: var(--bs-sidenav-width);
  transition: all 0.25s ease-in-out;
  border-bottom: 1px solid var(--bs-border-color);
  /* Search */
}
.app-topbar .topbar-menu {
  display: flex;
  justify-content: space-between;
  position: relative;
}
.app-topbar .topbar-menu .topbar-item {
  display: flex;
  align-items: center;
  justify-content: center;
  height: var(--bs-topbar-height);
  padding: 0 6px;
}
.app-topbar .topbar-menu .topbar-item .topbar-link {
  z-index: 1;
  border: none;
  background-color: transparent;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: var(--bs-topbar-item-color);
}
.app-topbar .topbar-menu .topbar-item .topbar-link:hover, .app-topbar .topbar-menu .topbar-item .topbar-link:active, .app-topbar .topbar-menu .topbar-item .topbar-link:focus {
  color: var(--bs-topbar-item-hover-color);
}
.app-topbar ul {
  list-style-type: none;
  margin-bottom: 0;
}
.app-topbar .logo {
  display: none;
}
.app-topbar .app-search {
  overflow: hidden;
  height: var(--bs-topbar-height);
  display: table;
  max-width: 180px;
  margin-right: 20px;
}
.app-topbar .app-search .app-search-box {
  display: table-cell;
  vertical-align: middle;
}
.app-topbar .app-search .app-search-box input::-webkit-input-placeholder {
  font-size: 0.8125rem;
  color: var(--bs-topbar-item-color);
}
.app-topbar .app-search .form-control {
  border: none;
  height: 38px;
  padding-left: 20px;
  padding-right: 0;
  color: var(--bs-topbar-item-color);
  background-color: var(--bs-topbar-search-bg);
  box-shadow: none;
  border-radius: 30px 0 0 30px;
}
.app-topbar .app-search .input-group-append {
  margin-left: 0 !important;
  z-index: 4;
}
.app-topbar .app-search .btn {
  background-color: var(--bs-topbar-search-bg);
  color: var(--bs-topbar-item-color);
  border-color: transparent;
  border-radius: 0 30px 30px 0;
  box-shadow: none !important;
  position: relative;
}

.sidenav-toggle-button {
  display: flex;
  position: relative;
  cursor: pointer;
  background-color: transparent;
  border: none;
  color: var(--bs-topbar-item-color);
}

html[data-bs-theme=dark] #light-dark-mode .ti-moon::before {
  content: "\eb30";
}

.nav-user {
  display: flex;
  align-items: center;
  gap: 10px;
  min-height: var(--bs-topbar-height);
  transition: none;
}

.topbar-search {
  width: 230px;
  padding: 0.5625rem 0.63rem;
  line-height: 1.5;
  background-color: var(--bs-topbar-search-bg);
  border-radius: 0.25rem;
}

.notification-item {
  background-color: var(--bs-secondary-bg);
  position: relative;
  cursor: pointer;
}
.notification-item .notification-item-close {
  position: absolute;
  right: 0;
  display: none;
  top: 0;
  bottom: 0;
  padding: 15px;
  background-color: var(--bs-tertiary-bg);
}
.notification-item .notification-badge {
  height: 22px;
  width: 22px;
  text-align: center;
  color: #fff;
  top: -12px;
  right: -8px;
  border: 2px solid var(--bs-secondary-bg);
  line-height: 18px;
  font-size: 11px;
}
.notification-item:hover .notification-item-close {
  display: block;
}

@media (max-width: 767.98px) {
  .app-topbar {
    padding: 0 0.5rem;
  }
  .logo-topbar {
    display: block;
  }
  .logo-topbar span.logo-lg {
    display: none;
  }
  .logo-topbar span.logo-sm {
    display: block;
  }
}
.noti-icon-badge {
  position: absolute;
  top: 3px;
  right: 4px;
  display: inline-block;
  height: 10px;
  width: 10px;
  background-color: #ff5d48;
  border-radius: 50%;
  border: 2px solid var(--bs-topbar-bg);
}

.sidenav-menu {
  z-index: 1000;
  position: fixed;
  bottom: 0;
  top: 0;
  width: var(--bs-sidenav-width);
  background: var(--bs-menu-bg);
  box-shadow: var(--bs-box-shadow);
  border-right: 0 solid var(--bs-border-color);
  transition: all 0.25s ease-in-out;
}
.sidenav-menu [data-simplebar] {
  height: calc(100% - var(--bs-topbar-height));
}

.side-nav {
  padding-left: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.side-nav .side-nav-item .side-nav-link {
  display: flex;
  align-items: center;
  gap: var(--bs-menu-link-item-gap);
  position: relative;
  white-space: nowrap;
  color: var(--bs-menu-item-color);
  transition: all 0.25s ease-in-out;
  line-height: var(--bs-menu-item-font-size);
  font-size: var(--bs-menu-item-font-size);
  padding: var(--bs-menu-item-padding-y) var(--bs-menu-item-padding-x);
  font-weight: 500;
}
.side-nav .side-nav-item .side-nav-link:hover, .side-nav .side-nav-item .side-nav-link:focus, .side-nav .side-nav-item .side-nav-link:active {
  color: var(--bs-menu-item-hover-color);
}
.side-nav .side-nav-item .side-nav-link .menu-icon {
  font-size: var(--bs-menu-item-icon-size);
  line-height: var(--bs-menu-item-icon-size);
}
.side-nav .side-nav-item .side-nav-link .menu-icon i {
  font-size: var(--bs-menu-item-icon-size);
  line-height: var(--bs-menu-item-icon-size);
}
.side-nav .side-nav-item .side-nav-link .menu-icon svg {
  height: var(--bs-menu-item-icon-size);
  width: var(--bs-menu-item-icon-size);
}
.side-nav .side-nav-item .side-nav-link .badge {
  margin-left: auto;
}
.side-nav .sub-menu {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 4px;
  padding-left: 0;
  margin-top: 5px;
}
.side-nav .sub-menu .side-nav-item .side-nav-link {
  font-size: calc(var(--bs-menu-item-font-size) * 0.95);
  padding: calc(var(--bs-menu-item-padding-y) * 0.95) calc(var(--bs-menu-item-padding-x) * 1.5) calc(var(--bs-menu-item-padding-y) * 0.95) calc(var(--bs-menu-item-icon-size) + var(--bs-menu-item-padding-x) + var(--bs-menu-link-item-gap));
}
.side-nav .sub-menu .sub-menu {
  padding-left: 12px;
}
.side-nav .side-nav-title {
  letter-spacing: 0.1em;
  pointer-events: none;
  cursor: default;
  white-space: nowrap;
  text-transform: uppercase;
  color: var(--bs-menu-item-color);
  font-weight: 700;
  font-size: calc(var(--bs-menu-item-font-size) * 0.7);
  padding: var(--bs-menu-item-padding-y) calc(var(--bs-menu-item-padding-x) * 1.25);
}
.side-nav > .side-nav-item.active .menu-arrow {
  transform: rotate(90deg);
}
.side-nav > .side-nav-item.active > a,
.side-nav > .side-nav-item.active .side-nav-link.active {
  color: var(--bs-menu-item-hover-color);
  font-weight: 500;
}

.menu-arrow {
  margin-left: auto;
  transition: transform 0.1s ease-in-out;
}
.menu-arrow:before {
  content: "\ea61";
  font-family: "tabler-icons";
}

[aria-expanded=true] .menu-arrow {
  transform: rotate(90deg);
}

.side-nav .side-nav-item .side-nav-link:hover, .side-nav .side-nav-item .side-nav-link:focus, .side-nav .side-nav-item .side-nav-link:active {
  background-color: var(--bs-menu-item-hover-bg);
}
.side-nav .side-nav-item .sub-menu {
  margin-top: 4px;
}
.side-nav > .side-nav-item.active > .side-nav-link {
  color: var(--bs-menu-item-active-color);
  background: var(--bs-menu-item-active-bg);
}

html[data-sidenav-size=condensed]:not([data-layout=topnav]) .app-topbar {
  z-index: 1005;
  margin-left: var(--bs-sidenav-width-sm);
}
html[data-sidenav-size=condensed]:not([data-layout=topnav]) .page-content {
  margin-left: var(--bs-sidenav-width-sm);
  min-height: 1500px;
}
html[data-sidenav-size=condensed]:not([data-layout=topnav]) .sidenav-menu {
  position: absolute;
  width: var(--bs-sidenav-width-sm);
